import React from 'react';
import {getTranslation} from '../../text/translate';
import './Conferences.css';

const Card = (props) => {
    const card = props.card;

    return (
        <div className={'conference-card'}>
            <h4 className={'conference-time'}>{card.time}</h4>
            <div className={'conference-info'}>
                <h3 className={'conference-title'}>{card.title}</h3>
                <h4>
                    <a target={'_blank'} href={card.contact} className={'conference-speaker'}>
                        {card.speaker}
                    </a>
                </h4>
                <h4 className={'conference-details'}>{card.details}</h4>
            </div>
        </div>
    )
}

const Conferences = (props) => {
    return (
        <div id={'conferences'} className={'page'}>
            <h4 className={'date'}>{getTranslation(props.language, 'conferences', 'date')}</h4>
            <h2 className={'title'}>{getTranslation(props.language, 'conferences', 'title')}</h2>
            <ul className={'details'}>
                <hr className={'conference-line'}/>
                {getTranslation(props.language, 'conferences', 'cards').map((item, index) =>
                    <div>
                        <Card key={index} card={item}/>
                        <hr className={'conference-line'}/>
                    </div>
                )}
            </ul>

            <div className={'button'}>
                <a className={'button-link'} target={'_blank'} href={'https://meet.zoho.eu/AKqB2WLKbe'}>
                    {getTranslation(props.language, 'conferences', 'link')}
                </a>
            </div>
        </div>
    )
}

export default Conferences;
